module.exports = "/*\r\n * Sidebar\r\n */\n.sidebar {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  background-color: #282828;\n  z-index: 100;\n  /* Behind the navbar */\n  padding: 24px 0 0;\n  /* Height of navbar */\n  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1); }\n.sidebar-sticky {\n  position: relative;\n  top: 0;\n  height: calc(100vh - 24px);\n  padding-top: 0.5rem;\n  overflow-x: hidden;\n  overflow-y: auto;\n  /* Scrollable contents if viewport is shorter than content. */ }\na {\n  color: #fff; }\n@supports ((position: -webkit-sticky) or (position: sticky)) {\n  .sidebar-sticky {\n    position: -webkit-sticky;\n    position: sticky; } }\n.sidebar .nav-link {\n  font-weight: 500;\n  color: #333; }\n.sidebar .nav-link .feather {\n  margin-right: 4px;\n  color: #999; }\n.sidebar .nav-link:hover .feather,\n.sidebar .nav-link.active .feather {\n  color: inherit; }\n.sidebar-heading {\n  font-size: 0.75rem;\n  text-transform: uppercase; }\nli.nav-item:hover {\n  background-color: #424141; }\n"